import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import PubSub from 'pubsub-js';
import { getAdditionalAPIs } from "../../commons/ExternalAPIUtil";

const dispatchedEvents = [];

/*
    Example: <srs-controller handlers=[{eventName: "XYZ", type: "script", script: `(function(eventPayload) { console.log(eventPayload)})`}]/>
*/
const SrsController = function (props, ref) {

    const findElement = (key, nestedContents) => {
        for (let element of nestedContents) {
            if (element.elementId === key) {
                return element;
            } else if (element.children) {
                const found = findElement(key, element.children);
                if (found) return found;
            }
        }
        return null;
    }

    const findParentElement = (elementId, nestedContents) => {
        const stack = [...nestedContents];

        while (stack.length > 0) {
            const element = stack.pop();

            if (element.elementId === elementId) {
                return null; // The provided elementId is already the top-level element, so no parent exists
            }

            if (element.children) {
                for (let i = 0; i < element.children.length; i++) {
                    const childElement = element.children[i];

                    if (childElement.elementId === elementId) {
                        return element; // Return the current element as the parent if a match is found
                    }

                    if (childElement.children) {
                        stack.push(childElement); // Add the child element to the stack for further iteration
                    }
                }
            }
        }

        return null; // Return null if no parent element is found
    };


    const removeElement = (contents) => {
        return (elementId, _contentItems) => {
            let contentItems = _contentItems || contents || [];
            let parentElement = findParentElement(elementId, contentItems);
            if (parentElement) {
                parentElement.children = parentElement.children.filter((childElement) => childElement.elementId != elementId);
            }
            if (_contentItems) {
                return contentItems;
            } else {
                props.dispatchEvent("PAGE_CONTENTS_REFRESH", { payload: contentItems });
            }
        }
    }

    const getElement = (contents) => {
        return (elementId) => {
            let contentItems = contents ? [...contents] : [];
            return findElement(elementId, contentItems);
        }
    }

    const replaceElement = (elementId, elementObj, contentsToBeUpdated) => {
        contentsToBeUpdated.forEach((element) => {
            if (element.elementId === elementId) {
                element = elementObj;
            } else if (element.children) {
                replaceElement(elementId, elementObj, element.children);
            }
        });
    }

    const setElement = (contents) => {
        return (elementId, elementObj, _contentItems) => {
            let contentItems = _contentItems || contents;
            let contentsToBeUpdated = contentItems ? [...contentItems] : [];
            replaceElement(elementId, elementObj, contentsToBeUpdated);
            if (_contentItems) {
                return contentsToBeUpdated;
            } else {
                props.dispatchEvent("PAGE_CONTENTS_REFRESH", { payload: contentsToBeUpdated });
            }
        }
    }

    const updateContents = (updatedClan) => {
        props.dispatchEvent("PAGE_CONTENTS_REFRESH", { payload: updatedClan });
    }


    useEffect(() => {
        const tokens = [];

        props.handlers && props.handlers.map((handler) => {
            let token = PubSub.subscribe(handler.eventName, async (eventName, eventData) => {
                if (handler.type) {
                    switch (handler.type) {
                        case "script":
                            let evaluateScript = true;
                            const context = {
                                eventPayload: eventData?.detail || {},
                                context: props.context || {},
                                clan: props.clan || [],
                                dispatchEvent: props.dispatchEvent,
                                api: {
                                    getElement: getElement(props.clan),
                                    setElement: setElement(props.clan),
                                    removeElement: removeElement(props.clan),
                                    findElement,
                                    findParentElement,
                                    updateContents,
                                    ...getAdditionalAPIs()
                                }
                            };
                            if(handler.condition) {
                                evaluateScript = false;
                                const conditionFn = new Function(...Object.keys(context), `return (${handler.condition})();`);
                                let conditionResult = await conditionFn(...Object.values(context));
                                if(conditionResult) {
                                    evaluateScript = true;
                                }
                            }
                            if(evaluateScript) {
                                const scriptFn = new Function(...Object.keys(context), `return (${handler.script})();`);
                                let scriptResult = await scriptFn(...Object.values(context));
                                // console.log(scriptResult);
                                if (scriptResult) {
                                    if (scriptResult.context) {
                                        props.dispatchEvent("PAGE_CONTEXT_REFRESH", { payload: scriptResult.context });
                                    }
                                    if (scriptResult.clan) {
                                        props.dispatchEvent("PAGE_CONTENTS_REFRESH", { payload: scriptResult.clan });
                                    }
                                }
                            }
                            break;
                        case "dispatch":
                            break;
                        case "navigate":
                            break;
                    }
                }

            });
            tokens.push(token);
        })


        return () => {
            tokens.map(token => PubSub.unsubscribe(token));
        };
    }, [props]);

    return (<div />);
}

const styles = `
`
const props = [{
    name: "handlers",
    defaultValue: [],
    type: "object"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsController), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };